<template>
  <div class="wrapper">
    <v-container>
      <v-row align="center">
        <v-col cols="12" md="8">
          <v-img class="shrink" src="@/images/forgetpassword.png" max-width="100%" />
        </v-col>
        <v-col cols="12" md="4">
          <v-row>
            <v-col>
              <v-img class="shrink" src="@/images/forgetpasswordtext.png" max-width="100%" />
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field
                dense
                hide-details
                label="Email (*)"
                placeholder="mail@domain.com"
                outlined
                v-model="email"
                ref="email"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field
                dense
                hide-details
                label="Phone Number (*)"
                placeholder="ex) 01012345678"
                outlined
                v-model="phoneNo"
                ref="phoneNo"
                oninput="javascript: this.value = this.value.replace(/\D/g, '' );"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field
                dense
                hide-details
                label="New password (*)"
                type="password"
                placeholder="Min. 6 character"
                outlined
                v-model="password"
                ref="password"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field
                dense
                hide-details
                label="Confirm password (*)"
                type="password"
                placeholder="New password again"
                outlined
                v-model="passwordRe"
                ref="passwordRe"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-btn depressed color="primary" width="100%" @click="setPassword"> Reset my password </v-btn>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<style scoped>
.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}
</style>

<script>
// eslint-disable-next-line no-unused-vars
import axios from "axios";
// eslint-disable-next-line no-unused-vars
import $ from "jquery";
import common from '@/common'

export default {
  data: () => ({
    email: "",
    phoneNo: "",
    password: "",
    passwordRe: "",
  }),
  methods: {
    setPassword() {
      if (!/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(this.email)) {
        this.$alert(common.messages.email, "", "info").then(() => {
          this.$refs.email.focus();
        });
        return false;
      }
      if (!this.phoneNo) {
        this.$alert(common.messages.phoneNo, "", "info").then(() => {
          this.$refs.phoneNo.focus();
        });
        return false;
      }
      if (!/^[\S]{6,}$/.test(this.password)) {
        this.$alert(common.messages.password, "", "info").then(() => {
          this.$refs.password.focus();
        });
        return false;
      }
      if (this.password != this.passwordRe) {
        this.$alert(common.messages.password, "", "info").then(() => {
          this.$refs.password.focus();
        });
        return false;
      }

      axios
        .put(common.makeAPIUrl("/prework/seller/password"),
          {
            email: this.email,
            phoneNo: this.phoneNo,
            password: this.password,
          },
          {
            //headers: {'Authorization': $("#authorization").val()}
          }
        )
        .then((response) => {
          if (response.status == 200) {
            this.$alert(common.messages.passwordModifyComplate, "", "info").then(() => {});
            this.$router.push("/login");
          }
        })
        .catch((err) => {
          this.$alert(err.response.data.message, "", "info").then(() => {});
        });
    },
  },
};
</script>
